<template>
  <div id="app">
    <Top :msg="msg" />
    <Footer v-for=" i in footer_data" :msg="i" :key="i.label1.href" />
  </div>
</template>

<script>
import Top from './components/Top';
import Footer from './components/Footer';
//import H2 from './components/H2';
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      footer_data:[
                    { 'no1':'ccc',

                      'label1':
                        [{'text':'labalccc1', 'href':'www.yahoo.com2'},
                        {'text':'labalccc2', 'href':'www.yahoo.com1'}]
                    },
                    { 'no1':'eee',
                      'label1':{'text':'labaleee', 'href':'www.gg.com'}
                    }
                  ]
      //msga: {'no1':'aaa', 'no2':'bbb'}
    }
  },
  components: {
    Top,
    Footer
    //H2
  }
}
</script>

<style lang="css" src="./assets/vue.css"></style>



