<template>
    <div>
        <h2>{{msg.no1}}</h2>
        <ul >
          <li v-for=" (value, index) in msg.label1" :key="value.text">
            <a :href="value.href" target="_blank" v-text="value.text"></a>
          </li>

        </ul>
    </div>
</template>

<script>
export default {
 props: {
  msg :{
    type: Object,
    default: ''
  }
 }
}
</script>


